}

.loader {
  width: 28px;
  aspect-ratio: 1;
  background: #f10c49;
  border-radius: 50%;
  animation: l2 1.5s infinite;
}

@keyframes l2 {
  0%, 100% {
    transform: translate(-35px);
    box-shadow: 0 0 #f4dd51, 0 0 #e3aad6;
  }

  40% {
    transform: translate(35px);
    box-shadow: -15px 0 #f4dd51, -30px 0 #e3aad6;
  }

  50% {
    transform: translate(35px);
    box-shadow: 0 0 #f4dd51, 0 0 #e3aad6;
  }

  90% {
    transform: translate(-35px);
    box-shadow: 15px 0 #f4dd51, 30px 0 #e3aad6;
  }
}

